
import XDialog from 'vux/src/components/x-dialog/index.vue'

export default {
components:{
XDialog
},
props:{
hidePanel:{

type:Function,
},
isShow:{
type:Boolean,
default:false
},
content:{
type:String,
default:''
}
},
data(){
return {
show:this.isShow,
style:{
height:'90%'
}
}
},
mounted(){
this.$refs.rule.style.height = this.getBoxHeight() + 'px';
},
methods:{
getBoxHeight(){
let height = document.body.offsetHeight;
let divide = height / 100;

return divide * 90;
},
dialogClose(){
this.show = false;
if(this.hidePanel){
this.hidePanel();
}
}
},
watch:{
isShow(){
this.show = this.isShow;
}
}
}



