import dialogRule from '@/components/dialogRule/dialogRule';
export default {
    components:{
        dialogRule
    },
    beforeRouteLeave(to, from, next){  // 用于做缓存
        //打开详情页（或者下一个任意界面）之前，把筛选条件保存到localStorage，如果离开列表页并且打开的不是详情页则清除，也可以选择不清除
        if (to.name == 'doctorAppointment') {
            // to.meta.keepAlive = true;
            localStorage.setItem('CRYBM' ,this.CRYBM)
            localStorage.setItem('YLJGID' ,this.YLJGID)
            localStorage.setItem('time' ,this.time);

        }else{
            localStorage.removeItem('CRYBM');
            localStorage.removeItem('YLJGID');
            localStorage.removeItem('time');
        }
        next();
    },
    data(){
        return {
            test:'',
            isDialog:false,
            isShow:false,
            CRYBM:'',       // 医生编码
            YLJGID:'',  // 机构编码
            doctorInfo:{},  // 医生详情
            Scheduling:{},  // 排版详情
            hdChangeBg:'rgba(60, 185, 99, 0)',
            text:`1、预约成功后，请患者于就诊当日携带有效证件、预约识别码及协和医院就诊卡到医院挂号窗口验证预约信息（核对与预约登记实名信息一致的本人有效证件和预约识别码）和取号，如验证不符则医院不能提供相应的诊疗服务。如果没有协和医院就诊卡者，请先办好就诊卡后再取号。<br>
2、取号时间：上午就诊患者，就诊当日早9：00以前取号。下午就诊患者，就诊当日下12:00-13:30之间取号。过时未取号者，预约作废。<br>
3、取号地点：西院区预约号取号地点：西院区门诊楼一层大厅挂号窗口取号。东院区预约号取号地点：东院区老门诊楼一层大厅挂号窗口或新门诊楼各楼层挂号/收费窗口取号。<br>
4、取号时间：上午就诊患者，就诊当日早9：00以前取号。下午就诊患者，就诊当日下12:00-13:30之间取号。过时未取号者，预约作废。`
        }
    },
    mounted() {
        this.setHeaderBg();

        var infoBox = this.$refs.infoBox;
        var info = this.$refs.info;
        var more = this.$refs.more;
        if(info.offsetHeight > infoBox.offsetHeight) {
            this.isShow = true;
            infoBox.style.paddingRight = '20px';
            var maxHeight = infoBox.offsetHeight;
            let show = true;
            infoBox.onclick = function(){
                if(show){
                    more.style.transform = 'rotate(-180deg)';
                    infoBox.style.maxHeight = 'none';
                    show = false;
                }else{
                    more.style.transform = 'rotate(0)';
                    infoBox.style.maxHeight = maxHeight + 'px';
                    show = true;
                }
                
            }
        }

        // 获取医生编码和机构编码
        this.CRYBM = this.$route.params.CRYBM || window.localStorage.CRYBM; // 医生编码
        this.YLJGID = this.$route.params.YLJGID || window.localStorage.YLJGID;  // 机构编码
        this.time = this.$route.params.time || window.localStorage.time;  // 时间

        this.init();  // 获取医生详情
    },
    methods:{
        setHeaderBg(){
            window.addEventListener('scroll',(event)=>{
                let scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop
                let opt = scrollTop/150>0.9?0.9:scrollTop/150;
                this.hdChangeBg = `rgba(60, 185, 99,${opt})`;             
            })
        },
        getYYtime(str){
            if(str){
                let time = str.split(' ')[0];
                let weekArr = ['周日','周一','周二','周三','周四','周五','周六']
                let week = weekArr[new Date(time).getDay()];
                return `${time} ${week}`

            }
        },
        starActivity(item,doctorInfo) {
             if(this.$root.loginInfo.isLogin==true){
                    localStorage.setItem('timeList', this.$route.params.item)
                    localStorage.setItem('doctorInfo', this.$route.params.doctorInfo)
                    this.$router.push({name:'doctorAppointment',params:{item:item,doctorInfo:doctorInfo}})
                }else{
                    if(this.$root.phoneType=='ios'){
                        window.webkit.messageHandlers.mutual.postMessage({type: "1"});
                    }else{
                        android:AndroidFun.mutual('1');
                    }
            }            
        },
        getCZYJSZC(str){
            switch(str){
                case '01':
                    return '主任医师'
                    break;   
                case '02':
                    return '副主任医师'
                    break;
                case '03':
                    return '主治医师'
                    break;
                case '04':
                    return '医师'
                    break;
                case '05':
                    return '医士'
                    break;             
            
            }
        },
        hidePanel(){
            this.isDialog = false;
        },
        init(){
            this.$ajax.post(this.$root.host,{
                "MSH.1": "doctorregistration",
                "MSH.2": "getDoctor",
                rybm: this.CRYBM,  // 医生编码
                jgbm: this.YLJGID,  // 机构编码
                time: this.time,  // 时间
                token:this.$root.loginInfo.token, //token               
            }).then(res => {
                if(typeof res.doctorInfo !='undefined'){
                    console.log(res)
                    this.doctorInfo = res.doctorInfo;
                }
                if(typeof res.timelist !='undefined'){
                    this.Scheduling = res.timelist;
                }
                
            }).catch(error => {
                console.log(error);
            })
        }
    },
}



